html {
  overflow-y: scroll;
  min-height: 100vh;
  background: rgb(121,121,121);
  background: radial-gradient(circle, rgba(121,121,121,1) 0%, rgba(54,54,54,1) 100%);
  color: #fff;
}

body {
  margin: 0;
  min-height: 100vh;
}

#root {
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

.container {
  max-width: 1280px;
  margin: auto;
}